            localStorage.setItem("kc",value);
            var mychart = this.$echarts.init(document.getElementById(value[0]));
            this.baroption.xAxis[0].axisLabel.rotate = value[1]
            this.baroption.xAxis[0].data = value[2]
            this.baroption.series[0].data = value[3]
            mychart.clear();
            mychart.setOption(this.baroption,true)
        }
    },
}
</script>

<style>
#xxkc{
    width: 100%;
    height: 100%;
}
#xxkc .row{
    display:flex;
    justify-content:center;
}
#xxkc .el-tabs--left .el-card__body{
    padding: 0!important;
}
#xxkc .row .col{
    width:750px;
    margin-top:20px;
}
#xxkc .row .cardtitle{
    font-size:25px;
    text-align:center;
    font-weight:900;
    margin-top:-20px;
    margin-bottom:-20px;
}
#xxkc .row .coltips{
    min-width:280px;
    max-width:300px;
    margin-top:20px;
}
#xxkc .row .coltips2{
    min-width:150px;
    max-width:300px;
    margin-top:20px;
}
#xxkc .shaixuan_box{
    position:relative;
    left:13%;
    bottom:32px;
}
#xxkc .shaixuan4{
    width: 400px;
    position:relative;
    left:13%;
    bottom:32px;
}
@media all and (max-width: 1200px) {
    #xxkc .shaixuan_box{
        left: 3%!important;
    }
}
#xxkc .row .tipheight{
    height:435px;
}
#xxkc .row .tip_select{
    height:465px;
}
#xxkc .row .tips{
    font-size:25px;
    text-align:left;
    font-weight:900;
    margin-top:-20px;
    margin-bottom:-20px;
    padding:0 20px;
}
#xxkc .row .tippoint{
    margin-top:50px;
    padding:0 20px;
    line-height:40px;
}
#xxkc .is-active{
    color: #029348 !important;
}
.el-tabs__active-bar{
    background: #029348 !important;
}
#xxkc .is-top :hover{
    color: #029348 !important;
}
#xxkc .is-left :hover{
    color: #029348 !important;
}
#xxkc .el-card{
    padding: 40px 0 40px 0;
}
#xxkc .shaixaun{
    padding: 82px 0 32px 0;
}
.el-tabs__content{
    overflow:inherit;
    padding-bottom: 20px;
}
.el-card{
    transition: 0s;
}
</style>



